import Vue from 'vue'
import Router from 'vue-router'
const login=() =>import('@/views/login')
const layout=() =>import('@/components/layout')
const main=() =>import('@/views/main')
Vue.use(Router)

// 固定的路由
export const fixedRouter =[{
  path:'',
  component:login,
  hidden:true
},
  {
    path:'',
    component:layout,
    children: [{
      path:'main',
      component:main,
      meta:{
        title:'首页',
        icon:'el-icon-info'
      }
    }]

  }
]

export default new Router({
  routes: fixedRouter
})



